import { Group } from '@visx/group';
import { Bar } from '@visx/shape';
import { scaleBand, scaleLinear, scaleOrdinal } from '@visx/scale';
import { AxisLeft, AxisBottom } from '@visx/axis';
import CustomTick from './CustomTick';

const margin = { top: 20, right: 20, bottom: 40, left: 150 };

const dataset = [
  {
    name: 'New York',
    population: 8336817,
    homicideRate: 5.5,
  },
  {
    name: 'Los Angeles',
    population: 3979537,
    homicideRate: 8.8,
  },
  {
    name: 'Chicago',
    population: 2693959,
    homicideRate: 28.5,
  },
  {
    name: 'Houston',
    population: 2316797,
    homicideRate: 17.2,
  },
  {
    name: 'Philadelphia',
    population: 1584064,
    homicideRate: 31.5
  }
];

const getHomicideRate = (d) => Number(d.homicideRate);


const colorScale = scaleOrdinal({
  domain: dataset.map((data) => data.name),
  range: ['#cfa9be', '#e6466d', '#f47b56', '#9a9fbc', '#dde34c'],
})

const compose = (scale, accessor) => data => scale(accessor(data));
const barColor = compose(colorScale, (d) => d.name);

const HorizontalBarChart = ({width, height}) => {
  const xMax = width - margin.left - margin.right;
  const yMax = height - margin.top - margin.bottom;

  const homicideRateScale = scaleLinear({
    range: [0, xMax],
    round: true,
    domain: [0, Math.max(...dataset.map(getHomicideRate))]
  });

  const cityScale = scaleBand({
    domain: dataset.map((data) => data.name),
    range: [0, yMax]
  })

  const axisScale = scaleBand({
    domain: dataset.map((data) => `${data.name} A city of ${data.population.toLocaleString("en-US")}`),
    range: [0, yMax]
  })

  // homicideRateScale.rangeRound([0, xMax]);
  const bars = dataset.map((d, i) => {
    const city = d.name;
    const barX = 0;
    const barY = cityScale(city);
    const barHeight = cityScale.bandwidth();
    const barWidth = homicideRateScale(getHomicideRate(d));
    const fill = barColor(d);
    return <>
      <Bar
        key={`bar-${i}`} 
        y={barY}
        x={barX}
        height={barHeight}
        width={barWidth}
        fill={fill}
      />
      <text 
        fill="#000" 
        fontSize={24}
        y={barY + 32}
        x={barWidth - (getHomicideRate(d).toString().length * 16)}
      >
        {getHomicideRate(d)}
      </text>
    </>;
  });
  return (
    <svg width={width} height={height}>
      <Group left={margin.left} top={margin.top}>
        {bars}
        <AxisLeft
          scale={axisScale}
          stroke='#000000'
          tickStroke='#000000'
          hideAxisLine
          tickLabelProps={() => ({
            fill: '#000000',
            fontSize: 16,
            textAnchor: 'end',
            dy: '0.33em',
          })}
          ticksComponent={(props) => {
            console.log(props);
            return <CustomTick props={props}  />
          }}
        />
        <AxisBottom
          scale={homicideRateScale}
          top={yMax}
          numTicks={0}
          hideAxisLine
          label="Homicides per 100,000 residents"
        />
      </Group>
    </svg>
  );
}

export default HorizontalBarChart;